import { Double } from "../types";
import { ChemObj } from "../core/ChemObj";
import { ifDef } from "../utils/ifDef";
/**
 *     .
 * @param chemObj    (, ,   ..)
 * @param applyAgentK   false,    .
 *
 *  1.    isAbstract    calcMass.
 * ..       ,   .
 *      .
 * ,   {R}-OH     O  H
 *
 *  2.          .
 *    ChemExpr    mass.
 */
export const calcMass = (
  chemObj: ChemObj,
  applyAgentK: boolean = true
): Double => {
  const stack: Double[] | [Double] = [0.0];
  const push = () => {
    stack.unshift(0.0);
  };
  const pop = (calc: () => Double) => {
    const value: Double = stack[0]! * calc();
    stack.shift();
    stack[0]! += value;
  };

  chemObj.walk({
    agentPre() {
      push();
    },

    agentPost(obj) {
      pop(() => (applyAgentK ? obj.n.num : 1.0));
    },

    mul() {
      push();
    },
    mulEnd(obj) {
      pop(() => obj.begin.n.num);
    },

    bracketBegin() {
      push();
    },
    bracketEnd(obj) {
      pop(() => obj.n.num);
    },

    nodePre() {
      push();
    },
    nodePost() {
      pop(() => 1.0);
    },

    itemPre() {
      push();
    },
    itemPost(obj) {
      //    $M()  ,  
      ifDef(obj.mass, (mass) => {
        stack[0] = mass;
      });
      pop(() => obj.n.num);
    },

    atom(obj) {
      stack[0] += obj.mass;
    },

    radical(obj) {
      obj.items.list.forEach((listItem) => {
        stack[0] += listItem.n * (listItem.elem?.mass ?? 0.0);
      });
    },
  });
  return stack[0]!;
};
