/* eslint-disable max-classes-per-file */
import { ChemObj } from "./ChemObj";
import { Visitor } from "./Visitor";
import { ChemK } from "./ChemK";
import { ChemNodeOptPair } from "./ChemNode";
/**
 *    1.1        
 * , CuSO4*5H2O.   .
 *  ,     .
 *   : https://ru.wikipedia.org/wiki/%D0%A1%D1%83%D0%BB%D1%8C%D1%84%D0%B0%D1%82_%D0%B6%D0%B5%D0%BB%D0%B5%D0%B7%D0%B0(III)-%D0%B0%D0%BC%D0%BC%D0%BE%D0%BD%D0%B8%D1%8F
 * Fe2(SO4)3 + (NH4)2SO4 + 24H2O "0^oC"-> 2NH4Fe(SO4)2*12H2O"|v"
 *   2     .
 *   ,       .
 *  -  : https://de.wikipedia.org/wiki/Rost
 *  x FeO  y FeO  z HO
 *  ,  .
 *    :      ?
 *  ,       .
 *         ,   
 * ('x'Fe(ii)O*'y'Fe(iii)2O3*'z'H2O)
 *  ,      .
 *        1.2!
 */

//  ,      
//  ,  , ..   
// example: CuSO4*5H2O
// isFirst  ,      (2FeO*3H2O)
export class ChemMul extends ChemObj {
  //     ,     .
  // .  "Ca(OH)2*3Mg(OH)2"   nodes=[undefined, "Mg"]
  nodes: ChemNodeOptPair = [undefined, undefined];

  constructor(
    public readonly n: ChemK,
    public readonly isFirst: boolean,
    public readonly color?: string
  ) {
    super();
  }

  override walk(visitor: Visitor) {
    visitor.mul?.(this);
  }
}

//  .
//    .
//    ,  ,   pop
export class ChemMulEnd extends ChemObj {
  constructor(public readonly begin: ChemMul) {
    super();
  }

  override walk(visitor: Visitor) {
    visitor.mulEnd?.(this);
  }
}
