import { isIdFirstChar } from "../parse/scanId";
import { defMacro } from "./defMacro";
import { PreProcCtx } from "./PreProcCtx";

// ,    .  ,      @:
//  @:A()...@()   @A()
//     ,   ,   @:  @A

export const bodyPreprocess = (ctx: PreProcCtx) => {
  for (;;) {
    const plain = ctx.search("@");
    if (plain === undefined) {
      //   
      ctx.writeFinish(); //      
      break;
    }
    ctx.write(plain); //   
    const c = ctx.n(); //  
    if (c === ":") {
      //   
      defMacro(ctx);
    } else if (isIdFirstChar(c[0]!)) {
      //   
      ctx.write(`@${c}`);
    } else {
      //      .     
      ctx.pos--;
      break;
    }
  }
};
